.header {
  padding-top: 60px;
}

.headerTitles {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Josefin Sans", sans-serif;
  font-style: italic;
}

.headerTitleSm {
  position: absolute;
  margin-top: 11px;
  font-size: 40px;
}

.headerTitleLg {
  position: absolute;
  margin-top: 20px;
  font-size: 110px;
}

.headerImg {
  width: 100%;
  height: 600px;
  margin-top: 150px;
  object-fit: cover 100;
}

@media only screen and (max-width: 768px) {
  .headerTitleLg {
    font-size: 90px;
  }

  .headerImg {
    height: 300px;
  }
}
