.post {
  width: 385px;
  margin: 0px 25px 40px 25px;
  display: flex;
  flex-direction: column;
}

.postImg {
  width: 385px;
  height: 280px;
  object-fit: cover;
  border-radius: 7px;
}

.postInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.postCat {
  font-family: "Lora", sans-serif;
  font-size: 11px;
  color: #222;
  line-height: 20px;
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
}

.postTitle {
  font-family: "Poppins", sans-serif;
  font-size: 20px;
  font-weight: 700;
  margin-top: 15px;
  cursor: pointer;
}

.postDate {
  font-family: "Lora", sans-serif;
  font-style: italic;
  font-size: 13px;
  color: #222;
  margin-top: 15px;
}

.postDescription {
  font-family: "Lora", sans-serif;
  font-size: 14px;
  line-height: 20px;
  margin-top: 15px;
  /* No more then 4 lines of text will be shown then ... */
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

@media only screen and (max-width: 768px) {
  .post {
    margin: 2px;
    padding: 0;
  }
  .postTitle {
    margin-top: 8px;
  }
  .postDescription {
    margin-top: 5px;
    margin-bottom: 25px;
  }
  .postDate {
    margin-top: 5px;
  }
}

@media only screen and (max-width: 428px) {
  .postImg {
    display: contents;
  }
}
